<template>
    <div
        class="pdd-countdown"
        v-if="countDownTime >= 0"
    >
        <span>
            <span>{{ ('0' + hours).slice(-2) }}</span>
            :
        </span>
        <span>
            <span>{{ ('0' + minutes).slice(-2) }}</span>
            :
        </span>
        <span>
            <span>{{ ('0' + seconds).slice(-2) }}</span>
            .
        </span>
        <span>
            <span>{{ decimal }}</span>
        </span>
    </div>
</template>

<script>
export default {
    name: 'PddCountdown',
    props: {
        time: {
            type: Number,
            required: true,
            default: 7 * 24 * 60 * 60 * 1000,
        },
        isStop: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            D: 24 * 60 * 60 * 1000,
            H: 60 * 60 * 1000,
            M: 60 * 1000,
            S: 1000,
            SS: 100,
            intervalId: 0,
            countDownTime: 0,
            previousTime: Date.now(),
        };
    },
    computed: {
        days() {
            const h = Math.floor(this.countDownTime / this.D);

            return h >= 0 ? h : 0;
        },
        hours() {
            const h = Math.floor((this.countDownTime - (this.days * this.D)) / this.H);

            return h >= 0 ? h : 0;
        },
        minutes() {
            const m = Math.floor((this.countDownTime - (this.days * this.D)
            - (this.hours * this.H)) / this.M);

            return m >= 0 ? m : 0;
        },
        seconds() {
            const s = Math.floor((this.countDownTime - (this.days * this.D)
            - (this.hours * this.H) - (this.minutes * this.M)) / this.S);

            return s >= 0 ? s : 0;
        },
        decimal() {
            const ss = Math.floor((this.countDownTime - (this.days * this.D)
            - (this.hours * this.H) - (this.minutes * this.M) - (this.seconds * this.S)) / this.SS);

            return ss >= 0 ? ss : 0;
        },
    },
    watch: {
        time: {
            handler(newVal) {
                if (newVal) {
                    setTimeout(() => {
                        this.countDownTime = newVal;
                        this.intervalId = setInterval(this.tick, 100);
                    }, 0);
                } else {
                    clearInterval(this.intervalId);
                }
            },
            immediate: true,
        },
    },
    destoryed() {
        clearInterval(this.intervalId);
    },
    methods: {
        tick() {
            const now = Date.now();
            const countDownTime = (now - this.previousTime);
            this.countDownTime = this.countDownTime - countDownTime;
            this.previousTime = now;

            if (this.isStop) {
                if ((this.countDownTime < 100 && this.countDownTime > 0)
                    || (this.countDownTime < 0 && this.countDownTime > -100)) {
                    this.$emit('stop');
                    clearInterval(this.intervalId);
                }
            }

            if (this.countDownTime < 0) {
                this.countDownTime = 0;
            }
        },
    },
};
</script>

<style scoped>
.pdd-countdown {
    display: flex;
    align-items: center;
    justify-content: center;
}

.pdd-countdown > span {
    display: flex;
    align-items: center;
    justify-content: center;
}

.pdd-countdown > span > span {
    width: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.pdd-countdown > span:last-child > span {
    width: 8px;
}
</style>
