<template>
    <div
        class="pdd-exchange"
        @click.capture="clickHandler"
    >
        <div
            class="back"
            @click="backHandler"
        />
        <exchange
            :options="options"
            :count-down="countDownTime"
            :number="number"
            :coin="currentCoin"
            :install="hasPinduoduo()"
            @showdialog="dialogHandler"
        />
        <goods-list
            :goods="goods"
            :options="options"
            @showdialog="dialogHandler"
        />
        <pdd-dialog
            :show="dialogType"
            :data="dialogData"
            :url="turnTableUrl"
            :install="hasPinduoduo()"
            @close="closeDialog"
        />
        <BackDialog
            v-if="backDialogShow"
            page="金币兑换现金券"
            url="bikan://web.goto/commonWebView?url_encode=https%3A%2F%2Ffeed.browser.miui.com%2Fmobile-v2%2Fact%2FpddCooperation"
            @close="backDialogShow = false"
        />
    </div>
</template>

<script>
import {
    doLogin,
    getApkVersion,
    quitCurrentWebview,
    isTakeOverBackByWeb,
} from '@/utils/jsApiV2/client';
import { addCustomEventListener, removeCustomEventListener } from '@/utils/event';
import { sget, sset } from '@/utils/cache';
import BackDialog from '@/components/BackDialog';
import { getBaseInfo } from '@/api/pinduoduo';
import GoodsList from './GoodsList';
import Exchange from './Exchange';
import PddDialog from './PddDialog';

const dialogSessionKey = 'pddExchange-backdialog';

export default {
    name: 'PddExchange',
    components: {
        GoodsList,
        Exchange,
        PddDialog,
        BackDialog,
    },
    data() {
        return {
            isLogin: false,
            currentCoin: 0,
            countDownTime: 0,
            number: 0,
            turnTableUrl: 'https://feed.browser.miui.com/mobile-v2/act/pddCooperation',
            options: [],
            goods: [],
            pinduoduo: 'com.xunmeng.pinduoduo',
            dialogType: '',
            dialogData: {},
            backDialogShowed: sget(dialogSessionKey) === 'true',
            backDialogShow: false,
        };
    },
    mounted() {
        addCustomEventListener('backKey', this.backKeyHandler);
        isTakeOverBackByWeb(true);
    },
    methods: {
        login() {
            try {
                doLogin('pddExchangeLoginCallback');
            } catch (e) {
                console.log(e);
            }
        },
        hasPinduoduo() {
            return getApkVersion(this.pinduoduo) > 0;
        },
        getData() {
            getBaseInfo().then((data) => {
                this.currentCoin = data.currentCoin || 0;
                this.countDownTime = data.remainingTime;
                this.number = data.number;
                this.turnTableUrl = data.turnTableUrl;
                this.options = data.options;

                if (!this.goods.length) {
                    this.goods = data.goods;
                }

                this.isLogin = data.login;
                if (!this.isLogin) {
                    this.login();
                }

                this.$root.hidePlaceholder();
            }).catch(() => {
                this.$error();
            });
        },
        closeDialog() {
            this.dialogType = '';
        },
        clickHandler(e) {
            if (!this.isLogin) {
                e.stopPropagation();
                this.login();
            }
        },
        dialogHandler(val) {
            this.dialogData = val;

            if (this.currentCoin >= val.coin) {
                this.dialogType = 'exchange';

                this.$o2o({
                    e_c: '兑换确认弹窗',
                    e_a: '曝光',
                    e_n: '金币兑换现金券',
                    e_x: {
                        keyword: `${val.cash}元`,
                    },
                });
            } else {
                this.dialogType = 'toturn';

                this.$o2o({
                    e_c: '金币不足弹窗',
                    e_a: '曝光',
                    e_n: '金币兑换现金券',
                    e_x: {
                        keyword: `${val.cash}元`,
                    },
                });
            }
        },
        focusHandler() {
            if (this.isLogin) {
                this.getData();
            }
        },
        backHandler() {
            this.$o2o({
                e_c: '返回按钮',
                e_a: '点击',
                e_n: '金币兑换现金券',
            });
            this.backKeyHandler();
        },
        backKeyHandler() {
            if (this.dialogType) {
                this.closeDialog();
            } else if (!this.backDialogShowed) {
                sset(dialogSessionKey, 'true');
                this.backDialogShowed = true;
                this.backDialogShow = true;
            } else if (this.backDialogShow) {
                this.backDialogShow = false;
            } else {
                quitCurrentWebview();
            }
        },
        addListeners() {
            window.pddExchangeLoginCallback = (success) => {
                if (success) {
                    this.getData();
                }
            };
            addCustomEventListener('focus', this.focusHandler);
        },
        init() {
            this.getData();
            this.$o2o({
                e_c: '主页',
                e_a: '曝光',
                e_n: '金币兑换现金券',
            });
        },
    },
    created() {
        this.addListeners();
        this.init();
    },
    destroyed() {
        isTakeOverBackByWeb(false);
        removeCustomEventListener('backKey', this.backKeyHandler);
        removeCustomEventListener('focus', this.focusHandler);
    },
};
</script>

<style scoped>
.pdd-exchange {
    position: relative;
    font-size: 0; /* 避免空格引起得间距值 */
}

.back {
    position: absolute;
    top: 5px;
    left: 2px;
    width: 37px;
    height: 37px;
    background: url(./images/back.webp) center no-repeat;
    background-size: 17px 17px;
    z-index: 1;
}
</style>
