<!-- eslint-disable max-len -->
<template>
    <gray-mask
        v-if="data && data.cash"
        :show="!!show"
    >
        <div class="dialog">
            <div
                class="close"
                @click="clickClose" />
            <div v-if="show==='exchange'">
                <div class="title">兑换确认</div>
                <div class="desc">即将兑换限时{{ data.cash }}元现金券，仅需0.1折（{{ data.coin }}金币），点击兑换按钮立即去使用现金券。</div>
                <div
                    class="button"
                    @click="clickExchange"
                >
                    立即兑换使用
                </div>
            </div>
            <div v-else>
                <div class="title">金币不足</div>
                <div class="desc">呀，账户金币不足，暂时无法兑换高额优惠券。推荐使用大转盘抽奖功能，领取金币和小额度优惠券再兑换。</div>
                <div
                    class="button"
                    @click="gotoTurn"
                >
                    去转盘抽免单
                </div>
            </div>
        </div>
    </gray-mask>
</template>

<script>
import { exchange } from '@/api/pinduoduo';
import { toast, openCommonWebView } from '@/utils/jsApiV2/client';
import GrayMask from '@/components/GrayMask';

export default {
    components: {
        GrayMask,
    },
    props: {
        show: {
            type: [String, Boolean],
            default: '',
        },
        data: {
            type: Object,
            default: () => {},
        },
        url: {
            type: String,
            default: '',
        },
        install: {
            type: Boolean,
            default: false,
        },
    },
    methods: {
        gotoTurn() {
            this.$o2o({
                e_c: '去大转盘按钮',
                e_a: '点击',
                e_n: '金币兑换现金券',
                e_x: {
                    keyword: `${this.data && this.data.cash}元`,
                },
            });

            location.href = this.url;
            this.$emit('close');
        },
        clickClose() {
            this.$emit('close');
            this.$o2o({
                e_c: this.show === 'exchange' ? '兑换确认弹窗' : '金币不足弹窗',
                e_a: '关闭',
                e_n: '金币兑换现金券',
                e_x: {
                    keyword: `${this.data && this.data.cash}元`,
                },
            });
        },
        clickExchange() {
            const type = this.data.cash === 10 ? 1 : 2;

            exchange({ type }).then(() => {
                if (this.data.linkUrl) {
                    openCommonWebView(this.data.linkUrl);
                } else if (this.install) {
                    location.href = this.data.installedUrl;
                } else {
                    openCommonWebView(this.data.uninstalledUrl);
                }

                this.$emit('close');
            }).catch(() => {
                toast('领取失败，请重试');
            });

            this.$o2o({
                e_c: '兑换并使用按钮',
                e_a: '点击',
                e_n: '金币兑换现金券',
                e_x: {
                    keyword: `${this.data && this.data.cash}元`,
                    status: this.install ? '已安装' : '未安装',
                },
            });
        },
    },
};
</script>

