<template>
    <div
        v-if="goods && goods.length"
        class="list"
    >
        <p class="list__title">商品样例</p>
        <div class="list__wrap">
            <div
                class="item"
                v-for="(item, key) in goods"
                :key="key"
                :link="item.linkUrl"
                @click="clickItem(item)"
            >
                <img
                    :src="item.imgUrl"
                    alt=""
                >
                <div class="item__title">{{ item.name }}</div>
                <div class="item__info">
                    <div class="desc">
                        =
                        <span class="desc__before">
                            <span>原价</span>
                            <i> ¥</i>{{ getOriginalPrice(item.originalPrice) }}
                        </span>
                        -
                        <span class="desc__discount">
                            <span>现金券</span>
                            <i> ¥</i>{{ item.discount }}
                        </span>
                    </div>
                    <div class="price">
                        <span class="price__title">券后价</span>
                        <span class="price__content">
                            <i>¥</i>{{ getPrice(item.originalPrice, item.discount) }}
                        </span>
                    </div>
                </div>
            </div>
        </div>
        <div class="list__footer">
            <div
                class="button"
                @click="clickButton"
            >
                <p>兑换查看全部 ></p>
            </div>
        </div>
    </div>
</template>

<script>
import { openCommonWebView } from '@/utils/jsApiV2/client';

export default {
    name: 'GoodsList',
    props: {
        goods: {
            type: Array,
            default: () => [],
        },
        options: {
            type: Array,
            default: () => [],
        },
    },
    methods: {
        getOriginalPrice(originalPrice) { // 原价
            return originalPrice.toFixed(1);
        },
        getPrice(originalPrice, discount) { // 售价
            const formateOriginalPrice = this.getOriginalPrice(originalPrice);
            let resultPrice = formateOriginalPrice - discount;

            if ((resultPrice * 10) % 10 > 0) { // 售价不是整数，保留2小数点
                resultPrice = resultPrice.toFixed(2);
            }

            return resultPrice;
        },
        /* eslint-disable no-param-reassign */
        clickItem(item = {}) {
            const i = item.discount === 20 ? 1 : 0;

            if (!this.options[i]) {
                return;
            }

            if (this.options[i].redeemed) {
                openCommonWebView(item.linkUrl);
            } else {
                item.cash = this.options[i].cash;
                item.coin = this.options[i].coin;

                this.$emit('showdialog', item);
            }

            this.$o2o({
                e_c: '商品详情点击',
                e_a: '点击',
                e_n: '金币兑换现金券',
                e_x: {
                    keyword: `${item.discount}元`,
                },
            });
        },
        clickButton() {
            if (this.options[0] && !this.options[0].redeemed) {
                this.$emit('showdialog', this.options[0]);
            } else if (this.options[1] && !this.options[1].redeemed) {
                this.$emit('showdialog', this.options[1]);
            } else if (this.options[0]) {
                openCommonWebView(this.options[0].uninstalledUrl);
            }

            this.$o2o({
                e_c: '底部按钮点击',
                e_a: '点击',
                e_n: '金币兑换现金券',
            });
        },
    },
};
</script>

