<template>
    <div
        class="exchange"
        v-if="options && options.length"
    >
        <div class="hint">
            <div class="hint-text">
                <span>今日优惠倒计时</span>
                <pdd-countdown
                    :time="countDown"
                    :is-stop="true"
                    @stop="$emit('stop')" />
                <span>已有{{ people }}人兑换使用</span>
            </div>
        </div>
        <div class="coupon-list">
            <div
                class="coupon coupon-breath"
                v-for="(item, key) in options"
                :key="key"
                :class="`coupon-${key}`"
                @click="clickCoupon(item)"
            >
                <div v-if="item.redeemed">
                    <div class="seal" />
                    <div class="button redeemed">
                        去使用
                    </div>
                </div>
                <div v-else>
                    <div class="button">
                        立即<br>兑换
                    </div>
                </div>
            </div>
        </div>
        <div class="rules">
            1、金币兑换的现金券，可用于[拼多多]APP购买商品使用，跳转后需登录领取；
            <br>
            2、每天仅能兑换<span>1次</span>，且需在<span>2小时</span>内使用，过期将不能使用；
            <br>
            3、现金券一经兑换，不可退换哦。
        </div>
    </div>
</template>

<script>
import { openCommonWebView } from '@/utils/jsApiV2/client';
import PddCountdown from './PddCountdown';

export default {
    name: 'Exchange',
    props: {
        options: {
            type: Array,
            default: () => [],
        },
        countDown: {
            type: Number,
            default: 0,
        },
        number: {
            type: Number,
            default: 0,
        },
        install: {
            type: Boolean,
            default: false,
        },
    },
    components: {
        PddCountdown,
    },
    computed: {
        people() {
            return this.number >= 10000 ? `${(this.number / 10000).toFixed(1)}万` : this.number;
        },
    },
    methods: {
        clickCoupon(item = {}) {
            if (item.redeemed) {
                if (this.install) {
                    location.href = item.installedUrl;
                } else {
                    openCommonWebView(item.uninstalledUrl);
                }

                this.$o2o({
                    e_c: `${item.cash}元券使用`,
                    e_a: '点击',
                    e_n: '金币兑换现金券',
                });
            } else {
                this.$emit('showdialog', item);

                this.$o2o({
                    e_c: `${item.cash}元券兑换`,
                    e_a: '点击',
                    e_n: '金币兑换现金券',
                });
            }
        },
    },
};
</script>

