import request, { apiResError } from './request';

export function getBaseInfo() {
    return request.get('/api/v4/pinduoduo/baseinfo')
        .then((response) => {
            if (response.data.status !== 200) {
                apiResError(response);
                return Promise.reject(response.data.data);
            }

            return Promise.resolve(response.data.data);
        });
}

export function exchange(data = {}) {
    return request.post('/api/v4/pinduoduo/exchange', data)
        .then((response) => {
            if (response.data.status !== 200) {
                apiResError(response);
                return Promise.reject(response.data.data);
            }

            return Promise.resolve(response.data.data);
        });
}
